import React, {useRef, useEffect, useState} from 'react';
import ReactDOM from 'react-dom';

const PopupWindow = props => {
  const divRef = useRef(document.createElement('div'));
  const windowRef = useRef();

  const [isMounted, setIsMounted] = useState(false);

  const copyStyles = (sourceDoc, targetDoc) => {
    Array.from(sourceDoc.styleSheets)
      .filter(
        styleSheet =>
          !styleSheet.href ||
          styleSheet.href.startsWith(window.location.origin),
      )
      .forEach(styleSheet => {
        if (styleSheet.cssRules) {
          const newStyleEl = sourceDoc.createElement('style');
          Array.from(styleSheet.cssRules).forEach(cssRule => {
            newStyleEl.appendChild(sourceDoc.createTextNode(cssRule.cssText));
          });
          targetDoc.head.appendChild(newStyleEl);
        }
      });
  };

  useEffect(() => {
    windowRef.current = window.open(
      '',
      props.windowName,
      'width=800,height=800,left=100,top=100',
    );
    windowRef.current.document.body.appendChild(divRef.current);
    windowRef.current.document.title = props.windowName;
    setIsMounted(true);
    return () => {
      windowRef.current.close();
    };
  }, []);

  useEffect(() => {
    if (isMounted) {
      windowRef.current.addEventListener('beforeunload', props.onClose);
      copyStyles(document, windowRef.current.document);
    }
  }, [isMounted]);

  if (!isMounted) {
    return null;
  }
  return ReactDOM.createPortal(<div>{props.children}</div>, divRef.current);
};

export default PopupWindow;
